@function calculateRem($size) {
	$remSize: $size / 16px;
	@return $remSize * 1rem; }

//Ð’Ñ‹ÑÑ‡Ð¸Ñ‚Ð°Ñ‚ÑŒ em Ð¸Ð· Ð¿Ð¸ÐºÑÐµÐ»ÐµÐ¹ - ÐºÐ¾Ñ€Ð¾Ñ‚ÐºÐ°Ñ Ð·Ð°Ð¿Ð¸ÑÑŒ
